html[dir="rtl"] body {
  text-align: right; }
  html[dir="rtl"] body * {
    letter-spacing: 0; }

.bottom_svg {
  right: calc(35vw - 2px);
  left: auto; }
  @media only screen and (max-width: 980px) {
    .bottom_svg {
      right: calc(50vw - 2px); } }

.home-banner .social_wrap {
  left: 1rem;
  right: auto; }

.banner_text h1 {
  font-size: clamp(3.5rem, 8vw, 7rem); }
  @media only screen and (max-width: 980px) {
    .banner_text h1 {
      font-size: clamp(2.5rem, 15vw, 7rem); } }
  .banner_text h1 div {
    transform: translateX(-8rem); }

.about_wrap .bg_svg {
  right: 0;
  left: auto; }

.about_wrap .img_2 {
  right: 0;
  left: auto; }
  .about_wrap .img_2::before {
    left: -1rem;
    right: auto; }

.ob_item {
  border-left: 2px dashed #E2503F;
  border-right: none; }
  .ob_item.swiper-slide-next ~ .ob_item {
    border-left: none; }
  @media only screen and (max-width: 980px) {
    .ob_item.swiper-slide-next {
      border-left: none; } }
  @media only screen and (max-width: 767px) {
    .ob_item {
      border: none; } }
  .ob_item .number_ {
    right: 0;
    left: auto; }

.title_tool_wrap .link_ {
  font-size: clamp(1rem, 1.4vw, 1.2rem); }
  @media only screen and (max-width: 1024px) {
    .title_tool_wrap .link_ {
      font-size: clamp(0.95rem, 6vw, 1.25rem); } }
